html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

body, html { height: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }
input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) { * { -webkit-text-size-adjust: none; } }
table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@font-face { font-family: "svgfont"; src: url("../fonts/svgfont.eot"); src: url("../fonts/svgfont.eot?#iefix") format("eot"), url("../fonts/svgfont.woff") format("woff"), url("../fonts/svgfont.woff2") format("woff2"), url("../fonts/svgfont.ttf") format("truetype"); font-weight: normal; font-style: normal; }
.icon, [class^="icon-"], [class*=" icon-"] { font-family: "svgfont"; font-style: normal; font-weight: normal; text-rendering: auto; speak: none; line-height: 1; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

.icon, [class^="icon-"], [class*=" icon-"] { display: inline-block; }

body { font-family: Arial, sans-serif; font-size: 12px; line-height: 1.4; min-width: 320px; color: #1a1a1a; background-color: #fff; overflow: hidden; }

.clearfix, .center-row { *zoom: 1; }

.clearfix:before, .center-row:before, .clearfix:after, .center-row:after { display: table; content: ""; line-height: 0; }

.clearfix:after, .center-row:after { clear: both; }

a, a:before, .button, button, button:before, input[type="button"], input[type="submit"] { transition: border-color 0.1s ease-in, color 0.1s ease-in, background-color 0.1s ease-in; }

img { text-decoration: none; }

button { background-color: transparent; cursor: pointer; }

input:-webkit-autofill { background: #000; background-color: red !important; }

.center-row { margin: 0 auto; position: relative; }

.container { margin: 0 auto; max-width: 1024px; box-sizing: border-box; padding: 0 20px; }

.row { margin: 0 20px; overflow: hidden; text-align: center; margin: 0 2%; }
@media screen and (max-width: 1200px) { .row { margin: 0 1%; } }

.righ-side-block { float: left; box-sizing: border-box; display: block; padding: 0 20px; }
@media screen and (min-width: 767px) and (max-width: 968px) { .righ-side-block { padding: 0 12px; } }
@media screen and (max-width: 767px) { .righ-side-block { width: 100%; } }

.loader-bg { background: url(data:image/gif;base64,) center center no-repeat; }

.log-out { position: absolute; right: 15px; top: 15px; z-index: 10; margin: 0; }

.float-left { float: left; }
@media screen and (max-width: 767px) { .float-left { float: none; } }

.float-right { float: right; }
@media screen and (max-width: 767px) { .float-right { float: none; } }

.righ-side-block { float: right; }

input[type="text"] { box-sizing: border-box; }

.hide { display: none !important; }

.logo-wrap, .start, .button, .social { -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; }

.justify { text-align: justify; line-height: 0; font-size: 0; text-justify: newspaper; zoom: 1; text-align-last: justify; }
.justify:after { content: ""; display: inline-block; width: 100%; height: 0px; visibility: hidden; overflow: hidden; }

h1 { font-weight: normal; font-size: 35px; color: #333; text-transform: uppercase; }

.logo-wrap { position: relative; left: 0; z-index: 6; height: 81px; text-align: center; display: table; table-layout: fixed; width: 100%; transition: all 400ms ease-in; height: 200px; }
@media screen and (max-width: 968px) { .logo-wrap { height: 150px; } }
.logo-wrap__i { display: table-cell; vertical-align: middle; width: 100%; height: 100%; }
.logo-wrap a { font-size: 18px; font-weight: 800; line-height: 1em; letter-spacing: 0.2em; text-transform: uppercase; color: #161a1b; text-decoration: none; display: inline-block; position: relative; }
.logo-wrap a img { max-height: 172px; transition: all 400ms ease-in; }
@media screen and (max-width: 968px) { .logo-wrap a img { max-height: 110px; } }

.preloader { display: block; position: fixed; width: 100%; height: 100%; z-index: 9999; background: white; text-align: center; }

.loading { overflow: hidden; position: absolute; top: 50%; margin-top: -12px; left: 50%; margin-left: -50px; width: 100px; height: 24px; }

.progress { display: block; position: absolute; bottom: 0; overflow: hidden; width: 100px; height: 1px; left: -102px; background-color: #00d9ec; -webkit-animation: loader-anim 1s 0s infinite cubic-bezier(0.785, 0.135, 0.15, 0.86); -moz-animation: loader-anim 1s 0s infinite cubic-bezier(0.785, 0.135, 0.15, 0.86); animation: loader-anim 1s 0s infinite cubic-bezier(0.785, 0.135, 0.15, 0.86); }

@-webkit-keyframes loader-anim { 0% { left: -102px; }
  100% { left: 102px; } }
@-moz-keyframes loader-anim { 0% { left: -102px; }
  100% { left: 102px; } }
@keyframes loader-anim { 0% { left: -102px; }
  100% { left: 102px; } }
@keyframes wave { 0% { -webkit-transform: rotateZ(0deg) translate3d(0, 10%, 0) rotateZ(0deg); -moz-transform: rotateZ(0deg) translate3d(0, 10%, 0) rotateZ(0deg); transform: rotateZ(0deg) translate3d(0, 10%, 0) rotateZ(0deg); }
  100% { -webkit-transform: rotateZ(360deg) translate3d(0, 10%, 0) rotateZ(-360deg); -moz-transform: rotateZ(360deg) translate3d(0, 10%, 0) rotateZ(-360deg); transform: rotateZ(360deg) translate3d(0, 10%, 0) rotateZ(-360deg); } }
body { background-image: url(../img/gifts.jpg); background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; background-attachment: fixed; }

.page-bg { position: absolute; left: 0; top: 0; overflow: hidden; z-index: 1; width: 100%; height: 100%; }
.page-bg__smoke img { transform: scale(0.6); }
@media screen and (min-width: 1200px) { .page-bg__smoke img { transform: scale(1); } }
.page-bg__smoke div { display: none; position: absolute; }
@media screen and (min-width: 1200px) { .page-bg__smoke div { display: block; } }
.page-bg__smoke-img1 { animation: wave 5s 0.1s infinite linear; display: none; }
@media screen and (min-width: 1200px) { .page-bg__smoke-img1 { display: block; position: absolute; right: 40%; top: 40%; } }
.page-bg__smoke-img2 { left: -3%; top: 15%; animation: wave 7s 0.1s infinite linear; }
@media screen and (min-width: 1200px) { .page-bg__smoke-img2 { left: -15%; top: -10%; } }
.page-bg__smoke-img3 { right: -25%; top: 3%; animation: wave 9s 0.1s infinite linear; }
@media screen and (min-width: 1200px) { .page-bg__smoke-img3 { right: 5%; top: -10%; } }

.page { height: 100%; position: relative; overflow-x: hidden; overflow-y: auto; z-index: 2; box-sizing: border-box; padding-bottom: 25px; }
@media screen and (max-width: 968px) { .page { margin-bottom: -25px; } }
.page__slider { position: absolute; top: 200px; bottom: 25px; text-align: center; width: 100%; }
@media screen and (max-width: 968px) { .page__slider { top: 150px; } }
.page__slider-content { display: table; width: 100%; height: 100%; table-layout: fixed; position: relative; padding-bottom: 25px; }
@media screen and (max-width: 968px) { .page__slider-content { padding-bottom: 0; } }
.page__slider-content-inner { display: table-cell; vertical-align: middle; width: 100%; height: 100%; padding: 1% 5% 0; }
@media screen and (max-width: 968px) { .page__slider-content-inner { padding: 3% 20px 30px; } }

.start { padding-bottom: 25px; }

.sub-title { font-size: 18px; margin-top: 5px; margin-bottom: 0px; color: #666; font-size: 3vw; transition: all 400ms ease-in; margin-bottom: 25px; margin: 2% 0; }
@media screen and (max-width: 968px) { .sub-title { font-size: 5.5vw; margin: 4% 0; } }

#countdown { text-align: center; font-size: 0; }
#countdown div { display: inline-block; padding: 0 34px; font-weight: 800; font-size: 50px; font-size: 8.5vw; box-sizing: border-box; text-align: center; transition: all 400ms ease-in; }
@media screen and (max-width: 968px) { #countdown div { padding: 0 10px; font-size: 12vw; width: 25%; } }
@media screen and (max-width: 480px) { #countdown div { font-size: 11.5vw; padding: 0 5px; } }
#countdown span { display: block; font-size: 50px; font-size: 3vw; line-height: 50px; letter-spacing: initial; font-weight: 100; text-transform: uppercase; transition: all 400ms ease-in; }
@media screen and (max-width: 767px) { #countdown span { font-size: 4vw; } }

form { display: block; margin: 0 auto; max-width: 450px; padding-bottom: 30px; }
@media screen and (max-width: 768px) { form { width: 100%; } }
form .form-line { margin: 15px 0 30px; position: relative; }
@media screen and (max-width: 1366px) { form .form-line { margin: 10px 0 25px; } }
form .form-line .error-notification { display: none; position: absolute; bottom: -18px; width: 100%; left: 0; color: #f03; line-height: 1; font-size: 16px; }
@media screen and (max-width: 1366px) { form .form-line .error-notification { font-size: 14px; } }
form .form-line.error .error-notification { display: block; }
form label { display: none; margin-bottom: 5px; font-size: 22px; color: #333; }
@media screen and (min-width: 1367px) { form label { display: block; } }
form input[type="text"], form input[type="email"], form input[type="password"] { display: inline-block; border-radius: 10px; padding: 0 10px; height: 46px; line-height: 46px; width: 100%; font-size: 18px; text-align: center; background-color: rgba(23, 26, 28, 0.3); border: 1px solid #171a1c; border: 1px solid rgba(23, 26, 28, 0.3); box-shadow: 0 0 25px #666 inset; color: #fff; }
@media screen and (max-width: 1366px) { form input[type="text"], form input[type="email"], form input[type="password"] { font-size: 16px; } }
form input[type="text"]:hover, form input[type="email"]:hover, form input[type="password"]:hover { box-shadow: 0 0 25px #666 inset, 0 0 10px #fff; }
form input[type="text"]:focus, form input[type="email"]:focus, form input[type="password"]:focus { box-shadow: 0 0 25px #666 inset, 0 0 25px #fff; }
form input[type="text"]::placeholder, form input[type="email"]::placeholder, form input[type="password"]::placeholder { color: #ddd; }
form .error input[type="text"], form .error input[type="email"], form .error input[type="password"] { box-shadow: 0 0 25px #666 inset, 0 0 25px #ff0033; color: #ff0033; }
form .or-form-separator { color: #333; font-size: 15px; font-weight: 600; display: inline-block; }
form .or-form-separator a { color: inherit; font: inherit; margin-left: 5px; }
form h1 { display: block; margin-bottom: 30px; font-size: 48px; }
@media screen and (max-width: 1367px) { form h1 { margin-bottom: 20px; font-size: 38px; } }

.two-step-form { width: 100%; padding: 0 25px; box-sizing: border-box; overflow: hidden; }
@media screen and (max-width: 968px) { .two-step-form { padding: 0; } }
.two-step-form__inner { width: 200%; transition: margin-left 400ms ease-in; }
.two-step-form__inner.show-next-form-step { margin-left: -100%; }
.two-step-form__step { width: 50%; display: inline-block; vertical-align: top; float: left; }
.two-step-form__step-inner { max-width: 450px; margin: 0 auto; }

.form-gender-button-wrap { font-size: 0; }
.form-gender-button-wrap input[type="radio"] { display: none; }
.form-gender-button-wrap label { display: inline-block; transition: all 400ms ease-in-out; cursor: pointer; position: relative; padding: 0 10px; height: 46px; line-height: 46px; width: 50%; font-size: 18px; text-align: center; background-color: rgba(23, 26, 28, 0.3); border: 1px solid #171a1c; border: 1px solid rgba(23, 26, 28, 0.3); box-shadow: 0 0 25px #666 inset; color: #fff; }
.form-gender-button-wrap__male { border-radius: 10px 0 0 10px; }
.form-gender-button-wrap__female { border-radius: 0 10px 10px 0; }
.form-gender-button-wrap input + label:hover { box-shadow: 0 0 25px #666 inset, 0 0 10px #fff; z-index: 1; }
.form-gender-button-wrap input:checked + label { z-index: 2; background-color: rgba(23, 26, 28, 0.8); box-shadow: 0 0 25px #666 inset, 0 0 25px #fff; }

.button { display: inline-block; text-decoration: none; font-size: 14px; font-weight: 700; letter-spacing: 0.2em; text-transform: uppercase; color: #161a1b; background: transparent; border: 1px solid #171a1c; border: 1px solid rgba(23, 26, 28, 0.3); border-radius: 20px; line-height: 16px; padding: 15px 40px; min-height: 46px; transition: all 0.9s; text-align: center; cursor: pointer; box-sizing: border-box; }
.button:hover { background: #171a1c; color: #fff; border: 1px solid #171a1c; transition: all 0.5s; box-shadow: 0 0 25px #fff; }
@media screen and (max-width: 968px) { .button { width: auto; margin: 0 auto; min-width: 20%; } }
@media screen and (max-width: 767px) { .button { display: block; width: 100%; max-width: none; } }

.social { font-size: 0; text-align: center; margin: 0 0 10px; }
.social-button { box-sizing: border-box; display: inline-block; vertical-align: top; margin: 0 30px; cursor: pointer; text-decoration: none; overflow: hidden; border-radius: 9px; transition: all 200ms ease-in; width: 60px; height: 60px; }
@media screen and (max-width: 1367px) { .social-button { width: 46px; height: 46px; } }
.social-button:hover { box-shadow: 0 0 25px 10px #fff; }
.social-button svg { width: 100%; height: 100%; transition: all 200ms ease-in; fill: rgba(23, 26, 28, 0.8); -webkit-filter: drop-shadow; }
.social-button:hover svg { fill: #171a1c; }

.registration-link { text-transform: lowercase; transition: all 200ms ease-in; }
.registration-link:hover { color: #fff; }

.taste-definition__header { color: #333; font-size: 40px; font-weight: normal; text-transform: none; margin: 1% 0; }
