.taste-definition__taste-category-wrap { width: 25%; display: inline-block; }
.taste-definition__category-name { color: #333; font-size: 25px; font-weight: normal; margin: 1% 0; }
@media screen and (max-width: 1200px) { .taste-definition__category-name { font-size: 15px; } }
.taste-definition__taste-image-wrap { border-radius: 20px; display: block; width: 100%; margin-bottom: 3%; padding-bottom: 100%; position: relative; border: 1px solid #171a1c; border: 1px solid rgba(23, 26, 28, 0.3); overflow: hidden; box-shadow: 0 0 25px 8px #666 inset; }
.taste-definition__taste-image-wrap:hover { box-shadow: 0 0 25px #666 inset, 0 0 25px 10px #fff; }
