.progress-bar { position: absolute; z-index: 6; top: 0; bottom: 25px; left: 1%; width: 4%; background-color: transparent; box-shadow: 0 0 25px #333 inset; border-radius: 5px; overflow: hidden; }
.progress-bar__inner { position: absolute; width: 100%; left: 0; bottom: 0; height: 0; transition: height 1s ease-out 0.5s; background-image: linear-gradient(rgba(0, 102, 255, 0.7), rgba(0, 204, 255, 0.7)); box-shadow: 0 0 25px #333 inset; border-radius: 5px; }

.result-page { max-width: 1024px; margin: 0 auto; box-sizing: border-box; padding: 20px; border-radius: 20px; background-color: rgba(23, 26, 28, 0.3); border: 1px solid #171a1c; border: 1px solid rgba(23, 26, 28, 0.3); box-shadow: 0 0 25px #666 inset; }
.result-page__top { margin-bottom: 3%; *zoom: 1; }
