.result-page__top .button { background-image: linear-gradient(rgba(0, 102, 255, 0.7), rgba(0, 204, 255, 0.7)); box-shadow: 0 0 25px #333 inset; border: none; color: #fff; }
.result-page__product + .result-page__product { margin: 3% 0 0; }
.result-page__product { overflow: hidden; background-color: #FFF; box-shadow: 0 0 25px #666; border-radius: 20px; padding: 20px; box-sizing: border-box; position: relative; z-index: 1; transition: all 400ms ease-in; }
.result-page__product-link-wrap { float: right; width: 30%; text-align: right; padding-top: 5%; box-sizing: border-box; }
