.result-page__product-image { float: left; text-align: center; width: 16%; padding-top: 16%; overflow: hidden; border-radius: 10px; box-shadow: 0 0 25px #666 inset; position: relative; }
@media screen and (max-width: 968px) { .result-page__product-image { width: 50%; padding-top: 50%; } }
@media screen and (max-width: 767px) { .result-page__product-image { float: none; margin: 0 auto 4%; } }
.result-page__product-image img { width: 100%; min-height: 100%; position: absolute; left: 0; top: 0; z-index: -1; }
.result-page__product-data { float: left; padding: 0 2%; text-align: left; width: 54%; box-sizing: border-box; }
