@media screen and (max-width: 968px) { .result-page__product-data { width: 50%; margin: 0 0 4%; } }
@media screen and (max-width: 767px) { .result-page__product-data { width: auto; float: none; } }
.result-page__product-name { font-weight: 700; font-size: 40px; line-height: 1; margin-bottom: 5px; }
